.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15% 0 15%;
}

.container {
  background-color: whitesmoke;
}

.main-header {
  display: inline-block;
  width: 100%;
  text-align: center;
  font-weight: lighter;
  font-size: 2em;
}

.main-content {
  padding: 0 18px;
  line-height: 2em;
  font-size: 1.2em;
  color: #999;
  margin-right: 10%;
}

.photo-container {
  width: 100%;
}

.photo {
  box-sizing: border-box;
  width: 400px;
  height: 400px;
  object-fit: cover !important;
  border-radius: 50%;
}

@media only screen and (orientation: portrait) {
  .photo {
    width: 100%;
    height: 100vw;
    padding: 0 10px 10px 10px;
  }
  .flex-container {
    flex-wrap: wrap;
    margin: auto;
  }

  .main-content {
    font-size: 1em;
    margin-right: 0;
    text-align: justify;
  }
}
